.CommandTable {
    width: 80%;
    border-collapse: collapse;
    margin-bottom: 15px;
}

.CommandTable th {
    padding: 10px;
    font-size: 24px;
    background-color: rgb(43, 142, 230);
    color: white;
}

.CommandTable td {
    padding: 10px;
    font-size: 14px;
}

.CommandTable td:first-child{
    width: 15%;
    max-width: 25%;
}

.CommandTable tr:nth-child(even) {
    background-color: rgb(248, 250, 252);
}

.CommandTable tr:nth-child(odd) {
    background-color: rgb(213, 216, 218);
}


.FlowTable {
    width: 80%;
    border-collapse: collapse;
    margin-bottom: 15px;
}

.FlowTable th {
    padding: 10px;
    font-size: 24px;
    background-color: rgb(35, 211, 108);
    color: white;
}

.FlowTable td {
    padding: 5px;
    font-size: 14px;
}

.FlowTable .Number{
    width: 20px;
}

.FlowTable tr:nth-child(even) {
    background-color: rgb(248, 250, 252);
}

.FlowTable tr:nth-child(odd) {
    background-color: rgb(213, 216, 218);
}

.SideBar {
    width: 80%;
    border-collapse: collapse;
    margin-bottom: 15px;
}

.SideBar th {
    padding: 10px;
    font-size: 24px;
    background-color: rgb(231, 218, 31);
    color: white;
}

.SideBar .Title{
    font-size: 18px;
    background-color: rgba(128, 128, 128, 0.479);
}

.SideBar td {
    padding: 5px;
    font-size: 14px;
    background-color: rgba(216, 216, 216, 0.452);
}

.Content {
    width: 80%;
    border-collapse: collapse;
    margin-bottom: 15px;
}

.Content th {
    padding: 10px;
    font-size: 24px;
    background-color: rgb(248, 96, 58);
    color: white;
}

.Content td {
    padding: 5px;
    font-size: 14px;
}

.Content td:first-child{
    width: 10%;
}

.Content .ContentField{
    white-space: pre-wrap;
}

.Content tr:nth-child(even) {
    background-color: rgb(248, 250, 252);
}

.Content tr:nth-child(odd) {
    background-color: rgb(213, 216, 218);
}

.OtherTable {
    width: 80%;
    border-collapse: collapse;
    margin-bottom: 15px;
}

.OtherTable th {
    padding: 10px;
    font-size: 24px;
    background-color: rgb(248, 144, 58);
    color: white;
}

.OtherTable td {
    padding: 5px;
    font-size: 14px;
}


.OtherTable .OtherContent{
    white-space: pre-wrap;
}



@media screen and (max-width: 480px) {
    .CommandTable {
        width: 100%;
        border-collapse: collapse;
        margin-bottom: 15px;
    }
    
    .CommandTable th {
        padding: 8px;
        font-size: 16px;
        background-color: rgb(43, 142, 230);
        color: white;
    }
    
    .CommandTable td {
        padding: 8px;
        font-size: 12px;
    }
    
    .CommandTable td:first-child{
        width: auto;
        max-width: 25%;
    }
    
    .CommandTable tr:nth-child(even) {
        background-color: rgb(248, 250, 252);
    }
    
    .CommandTable tr:nth-child(odd) {
        background-color: rgb(213, 216, 218);
    }
    
    
    .FlowTable {
        width: 100%;
        border-collapse: collapse;
        margin-bottom: 15px;
    }
    
    .FlowTable th {
        padding: 8px;
        font-size: 16px;
        background-color: rgb(35, 211, 108);
        color: white;
    }
    
    .FlowTable td {
        padding: 5px;
        font-size: 12px;
    }
    
    .FlowTable .Number{
        width: 20px;
    }
    
    .FlowTable tr:nth-child(even) {
        background-color: rgb(248, 250, 252);
    }
    
    .FlowTable tr:nth-child(odd) {
        background-color: rgb(213, 216, 218);
    }
    
    .SideBar {
        width: 100%;
        border-collapse: collapse;
        margin-bottom: 15px;
    }
    
    .SideBar th {
        padding: 8px;
        font-size: 16px;
        background-color: rgb(231, 218, 31);
        color: white;
    }
    
    .SideBar .Title{
        font-size: 14px;
        background-color: rgba(128, 128, 128, 0.479);
    }
    
    .SideBar td {
        padding: 5px;
        font-size: 12px;
        background-color: rgba(216, 216, 216, 0.452);
    }
    
    .Content {
        width: 100%;
        border-collapse: collapse;
        margin-bottom: 15px;
    }
    
    .Content th {
        padding: 8px;
        font-size: 16px;
        background-color: rgb(248, 96, 58);
        color: white;
    }
    
    .Content td {
        padding: 5px;
        font-size: 12px;
    }
    
    .Content td:first-child{
        width: 25%;
    }
    
    .Content .ContentField{
        white-space: pre-wrap;
    }
    
    .Content tr:nth-child(even) {
        background-color: rgb(248, 250, 252);
    }
    
    .Content tr:nth-child(odd) {
        background-color: rgb(213, 216, 218);
    }
    
    .OtherTable {
        width: 100%;
        border-collapse: collapse;
        margin-bottom: 15px;
    }
    
    .OtherTable th {
        padding: 8px;
        font-size: 16px;
        background-color: rgb(248, 144, 58);
        color: white;
    }
    
    .OtherTable td {
        padding: 5px;
        font-size: 12px;
    }
    
    
    .OtherTable .OtherContent{
        white-space: pre-wrap;
    }
}